<script setup>
import { reactive, ref, computed } from "vue";
import AppAccordion from "@/components/AppAccordion.vue";

const contests = reactive([])
const cand = reactive([])
const mySearch = ref("")
const favorites = ref("")
const autocomplete = ref(false);

fetch(window.APP_URL + "/data/scaffold.json")
  .then((res) => res.json())  
  .then((data) => {
    data.forEach(el => {
      contests.push(el)
    });
  })

fetch(window.APP_URL + "/data/candidates.json")
  .then((res) => res.json())  
  .then((data) => {
    data.forEach(el => {
      cand.push(el)
    });
  })

const filterCandidates = computed(() => {
  if (mySearch.value.length >= 4) {
    return cand.filter(item => {
      return item.toLowerCase().indexOf(mySearch.value.toLowerCase()) != -1
    })
  } else {
    return []
  }
})

function updateSearch(btn) {
  autocomplete.value = false
  mySearch.value = btn
}

const filterContests = computed(() => {
  return contests.filter((contest) => {
    let contestFilter = contest.data.filter((s) => {
      return (
        s.candidates
          .join(", ")
          .toLowerCase()
          .indexOf(mySearch.value.toLowerCase()) != -1
      );
    });

    if (!contestFilter.every((con) => con === true)) {
      return contests;
    }
  });
});

function removeFiltered(con) {
  return con.filter((cand) => {
    let candFilter = cand.candidates.filter((candidate) => {
      if (candidate.toLowerCase().includes(mySearch.value.toLowerCase())) {
        return con
      }
    })

    if (!candFilter.every((can) => can === true)) {
      return con;
    }
  })
}

function deleteContests() {
  localStorage.contests = ''
  var checkbox = document.querySelectorAll("input[type=checkbox]");
  checkbox.forEach(el => {
    el.checked = false
  })
}

function exportContests() {
  if (localStorage.getItem('contests').length > 0) {
    favorites.value = localStorage.getItem('contests')
  } else {
    favorites.value = 'No Contests Selected - to select a contest down below click a location and then a desired contest.'
  }
}

function copyLink() {
  var input = document.getElementById("exportFavorites");
  input.select();
  input.setSelectionRange(0, 99999); 
  navigator.clipboard.writeText(input.value)
}

function exportToFile() {
  var link = document.createElement('a');
  link.href = 'data:text/plain;charset=UTF-8,' + escape(favorites.value);
  link.download = 'my-favorite-contests.txt';
  link.click();
}

function importText() {
  var input = document.getElementById("importFavorites");
  
  if (input.value.length > 0) {
    updateStorage(input.value)
    input.value = '' // reset value back to default
  } else {
    alert('You have to paste a list of contests.')
  }
}

function importFromFile() {
  var input = document.createElement('input');
  input.setAttribute('type', 'file');

  input.addEventListener("change", () => {
    var file = input.files[0]
    if (file.type.match(/text.*/)) {
      var reader = new FileReader();
      reader.onload = function() {
        var text = reader.result
        updateStorage(text)
        alert("Your selected Contests were sucessfully imported.")
      }
      reader.readAsText(file)
    } else {
      alert("File type is not supported.")
    }
  })
  input.click()  
}

function updateStorage(text) {
  var notNumber = []

  text.split(',').forEach(element => {
    var checkbox = document.querySelector("input[value='" + element + "']");
    if (Number.isInteger(parseInt(element))) {
      if (checkbox) { checkbox.checked = true }  
    } else {
      notNumber.push(element)
    }
  });

  if (notNumber.length < 1) {
    localStorage.contests = text
  }
}

</script>

<template>
  <main>
    <div class="container-fluid">
      <div class="row header">
        <div class="col-sm-12 col-md-6">
          <h1 class="contest-type">Create a Custom List</h1>
        </div>
        <div class="col-sm-12 col-md-6 text-end data-buttons">
          <button type="button" class="btn btn-primary" @click="exportContests" data-bs-toggle="modal" data-bs-target="#exportModal">Export</button>
          <button type="button" class="btn btn-secondary" @click="importContests" data-bs-toggle="modal" data-bs-target="#importModal">Import</button>
          <button type="button" class="btn btn-danger" @click="deleteContests">Delete</button>
        </div>
      </div>
      <div class="container">
        <p>
          Clicking on a category below will expand the choice to give you a list of all available contest within that option. 
          Check off all the contest you wish to view and hit the submit button at the bottom of the page. This will provide 
          you a custom view of only races you're interested in. If you aren't sure who the candidates are in the contest, clicking 
          on the info button to the right will provide a list of all the candidates.
        </p>
      </div>
      <div class="row">
        <div id="Searchbar" class="d-flex justify-content-center mb-4 p-4">
          <input type="text" placeholder="search candidates..." class="form-control form-control-lg" v-model="mySearch" @focus="autocomplete=true"/>
          <div id="myInputautocomplete-list" class="autocomplete-items list-group text-left" v-if="autocomplete">
            <li class="list-group-item list-group-item-action" 
                v-for="(candidate, index) in filterCandidates" :key="index" @click="updateSearch(candidate)">{{ candidate }}
            </li>
          </div>
        </div>
      </div>
      <div class="row">
        <div class="container mb-4">
          <div v-for="(contest, index) in filterContests" :key="index">
            <AppAccordion :electionName="contest.location" :idx="index" :contestData="removeFiltered(contest.data)" />
          </div>
        </div>
      </div>
    </div>
  </main>

  <!-- Export Modal -->
  <div class="modal fade" id="exportModal" tabindex="-1" aria-labelledby="exportModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exportModalLabel">Export Contests</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <textarea type="text text-break" class="form-control" :value="favorites" id="exportFavorites" rows="7" readonly></textarea>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" @click="copyLink" data-bs-dismiss="modal">Copy Text</button>
          <button type="button" class="btn btn-primary" @click="exportToFile" id="download">Export to File</button>
        </div>
      </div>
    </div>
  </div>

  <!-- Import Modal -->
  <div class="modal fade" id="importModal" tabindex="-1" aria-labelledby="importModalLabel" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="importModalLabel">Import Contests</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <textarea type="text text-break" class="form-control" value="" 
            id="importFavorites" rows="7" placeholder="paste contests or upload file...">
          </textarea>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" @click="importText" data-bs-dismiss="modal">Import Text</button>
          <button type="button" class="btn btn-primary" @click="importFromFile">Import File</button>
        </div>
      </div>
    </div>
  </div>

</template>

<style scoped>

.contest-type {
  text-align: left;
  height: 30px;
  line-height: 30px;
}

.btn {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}

@media (max-width: 765px) {
  .data-buttons {
    text-align: center !important;
    margin-top: 0.5rem;
  }
  .contest-type {
    text-align: center !important;
  }
}

#Searchbar {
  position: relative;
  display: inline-block;
}

.autocomplete {
	position: relative;
	display: inline-block;
}
.autocomplete-items {
	position: absolute;
	z-index: 99;
	top: 75%;
	left: 0;
	right: 0;
  padding-left: 1.5rem !important;
  padding-right: 1.5rem !important;  
}


</style>